import './Card.css';
import Card from './Card';
import Header from './Header';
import './App.css';

import Sdata from './Sdata';

function ncard(val){
  return(
    
          
          <div className="col-md-12 col-lg-3 col-12">
            <Card  
              img={val.img} 
              title={val.title} 
              link={val.link} 
              alt={val.alt}/>
          </div>
                  
        
    
  );
}

function App(){

  return(
    <>
      

        <Header/>
        <div className="row">
        {Sdata.map(ncard)};
        </div>
        
    </>
  );
  
}

export default App;